<template>
  <div class="editorView-container" :style="{width: `${width}px`, height: `${height}px`}">
    <three-view :width="width" :height="threeViewHeight"></three-view>
    <view-control :width="width" :height="viewControlHeight"></view-control>
  </div>
</template>

<script>
// 试图渲染
import threeView from './editorView/threeView'
// 试图控制
import viewControl from './editorView/viewControl'
export default {
  props: {
    width: Number, // 试图区宽度
    height: Number // 视图区高度
  },
  components: {
    threeView, // 试图渲染
    viewControl // 试图控制
  },
  computed: {
    // 渲染区高度
    threeViewHeight () {
      return this.height - this.$store.getters.getViewControlHeight
    },
    // 控制层高度
    viewControlHeight () {
      return this.$store.getters.getViewControlHeight
    }
  }
}
</script>

